import React, {useEffect, useRef} from 'react';
import styles from './FavoriteProperties.module.css';
import Flickity from 'flickity';
import 'flickity/css/flickity.css';
import {PropertyMainFields} from "../../../data/model/PropertiesModel";

interface FavoritePropertiesProps {
    favoriteProperties: PropertyMainFields[]
}

const FavoriteProperties: React.FC<FavoritePropertiesProps> = ({favoriteProperties}) => {
    const carouselRef = useRef<HTMLDivElement | null>(null);
    const flickityOptions = {
        initialIndex: 0,
        freeScroll: true,
        contain: true,
        pageDots: false,
        prevNextButtons: true,
        autoPlay: 1500,
        wrapAround: true,
    };

    const truncateDescription = (description: string, maxLength = 500) => {
        if (!description) return '';  // Check if description is empty
        return description.length > maxLength
            ? description.slice(0, maxLength) + '...'
            : description;  // Append "..." only if truncated
    };

    useEffect(() => {
        if (carouselRef.current) {
            const flkty = new Flickity(carouselRef.current, flickityOptions);

            return () => {
                flkty.destroy();
            };
        }
    }, [flickityOptions]);


    return (
        <div className="carousel-container" ref={carouselRef}>
            {favoriteProperties.map((property, index) => (
                <a
                    key={index}
                    href={property.url} // Set the href to property.url
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles.link}
                    style={{textDecoration: 'none', color: 'inherit'}}
                >
                    <div className="carousel-cell" key={index}>
                        <div className={styles.carouselCellContainer}>
                            <img src={property.mainPhoto} alt={''} />
                            <div>
                                <h3>{property.title}</h3>
                                <h5>{truncateDescription(property.description, 350) || "No description available"}</h5>
                            </div>
                            <h2>{property.price}</h2>
                        </div>
                    </div>
                </a>
            ))}
        </div>
    );
};

export default FavoriteProperties;
