import React from 'react';
import styles from './AboutUs.module.css';

const AboutUs = () => {
    return (
        <div className={styles.aboutUsContainer}>
            <div className={styles.aboutUsItem}>
                <h3 className="title">Monteurzimmer fr jede Situation</h3>
                <p className="text">Wir bieten dir eine breite Auswahl an Monteurzimmern in ganz Deutschland - egal, ob du in Berlin, Mnchen oder einer anderen Stadt eine Unterkunft suchst. Unsere Plattform bietet dir die Mglichkeit, die Preise und Verfgbarkeiten der Zimmer schnell und einfach zu vergleichen. Du entscheidest, was fr dich am besten passt, ohne Zeit zu verlieren. Unser Ziel ist es, dir bei der Suche nach dem idealen Monteurzimmer zu helfen, damit du dich voll auf deine Arbeit konzentrieren kannst. Einfach, schnell und zuverlssig - so findest du das passende Zimmer.</p>
            </div>
            <div className={styles.aboutUsItem}>
                <h3 className="title">Individuelle Lsungen fr Teams</h3>
                <p className="text">Unsere Plattform ist ideal fr Teams und groe Gruppen, die gemeinsam auf Montage oder einem Bauprojekt arbeiten. Mit unserer Suchfunktion kannst du Unterknfte fr Gruppen ab 10 Personen finden und sofort buchen. Ob du eine komfortable Unterkunft fr deine Mitarbeiter in Stuttgart, Leipzig oder Hamburg bentigst - bei uns findest du das passende Angebot. Unsere flexiblen Buchungsoptionen ermglichen es dir, die Aufenthaltsdauer an dein Projekt anzupassen, und bieten ausreichend Platz fr alle. So sorgst du fr eine stressfreie Unterkunftslsung fr dein gesamtes Team.</p>
            </div>
            <div className={styles.aboutUsItem}>
                <h3 className="title">Schnell, einfach und preiswert</h3>
                <p className="text">Zeit ist Geld - das wissen wir, besonders bei Bauprojekten oder Montagen. Daher haben wir es uns zur Aufgabe gemacht, dir eine Plattform zu bieten, auf der du mit nur wenigen Klicks die besten Monteurzimmer und Arbeiterunterknfte findest. Dank unserer modernen Technologie kannst du Angebote nach Preis, Verfgbarkeit und Lage filtern und direkt online buchen. Ob in Essen, Kln oder Frankfurt - wir helfen dir, die beste Lsung fr dein Budget zu finden. So sparst du Zeit und kannst dich auf das Wesentliche konzentrieren: deine Arbeit.</p>
            </div>
        </div>
    );
};

export default AboutUs;
